:root {
  --theme-button-text: rgb(255, 255, 255);
  --light:  rgb(219, 215, 212);
  --semidark: rgb(80, 80, 80);
  --dark: rgb(36, 36, 36);
  --accent-bright: rgb(255, 69, 0);
  --accent-complement: rgb(8, 30, 103);
  --transparent: rgba(0,0,0,0);
  --cucumber-water: linear-gradient(90deg, rgba(227, 255, 231, 0.1) 0%, rgba(217, 231, 255, 0.2) 100%);
  --linear-gradient:
      linear-gradient(127deg, rgba(57, 104, 175, 0.3), rgba(0,255,0,0) 70.71%),
      linear-gradient(45deg, rgba(247, 76, 232, 0.3), rgba(255, 0, 0, 0) 70.71%),
      linear-gradient(210deg, rgba(0, 255, 200, 0.3), rgba(0,0,255,0) 70.71%);

  --bg-light: url(/static/media/layered-waves-haikei.0d600221.svg);
  --bg-dark: url(/static/media/layered-waves-haikei-blue-red.44172255.svg);


  font-family: 'Proxima Nova', 'Trebuchet MS', Helvetica, sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
#root {
  height: 100%;
}

*, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  height: 100vh;
}

h1 {
  font-size: 5vh;
  font-family: 'Proxima Nova Semibold', 'Trebuchet MS', Helvetica, sans-serif;
}

h3 {
  font-size: 3vh;
  font-family: 'Proxima Nova', 'Trebuchet MS', Helvetica, sans-serif;
}

.light {
  --theme-page-background: var(--light);
  --theme-page-text: var(--light);
  --theme-button-color: var(--accent-complement);
  --theme-same: var(--light);
  --theme-diff: var(--dark);
  --theme-mid: var(--semidark);
  background: url(/static/media/layered-waves-haikei.0d600221.svg);
  background: var(--bg-light);
}

.dark {
  --theme-page-background: var(--dark);
  --theme-page-text: var(--light);
  --theme-button-color: var(--accent-bright);
  --theme-same: var(--dark);
  --theme-diff: var(--light);
  --theme-mid: var(--semidark);
  background: url(/static/media/layered-waves-haikei-blue-red.44172255.svg);
  background: var(--bg-dark);
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 2em;
}

.badges {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column wrap;
            flex-flow: column wrap;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transform-origin: center;
            transform-origin: center;
}

.row {
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: .25em;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

a {
    margin: 0.2em;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App {
    overflow: hidden;
  }
  .main {
    -webkit-flex: 1 1;
            flex: 1 1;
    width: 80%;

  }

  .badges {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
}

/* Small devices (portrait tablets and large phones, 600 and up) */
@media only screen and (min-width: 600px) {
  .App {
    overflow: hidden;
  }
  .main {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items:center;
            align-items:center;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .badges {
    -webkit-transform: scale(1.75);
            transform: scale(1.75);
  }
}


