@-webkit-keyframes move {
  0% {
    offset-distance: 0%;
  }
  100% {
    offset-distance: 100%;
  }
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}

/* hotend fan */
.hotend:before {
    content: '';
    width: 30px;
    height: 30px;
    position: absolute;
    background-color: var(--theme-mid);
    border: 2.5px dotted var(--light);
    -webkit-filter: contrast(55%);
            filter: contrast(55%);
    border-radius: 50%;
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
}

/* hotend nozzle */
.hotend:after {
    content: '';
    width: 30px;
    height: 20px;
    position: absolute;
    background-color: var(--theme-mid);
    -webkit-filter: contrast(25%);
            filter: contrast(25%);
    -webkit-clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    -webkit-transform: translate(7.5px, 45px);
            transform: translate(7.5px, 45px);
}

